/* Components */
/* Main Styles */
.button {
  display: block;
  align-self: flex-end;
  background-color: #dd4b39;
  width: 120px;
  height: 30px;
  line-height: 31px;
  color: #fff !important;
  position: relative;
  cursor: pointer;
  overflow: hidden;
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.2);
  transition: all 0.25s cubic-bezier(0.31, -0.105, 0.43, 1.4);
  border-radius: 40px; }
  .button span, .button .icon {
    display: block;
    height: 100%;
    text-align: center;
    position: absolute;
    top: 0; }
  .button span {
    width: 78%;
    line-height: inherit;
    font-size: 0.75rem;
    font-weight: 400;
    text-transform: uppercase;
    left: 0;
    transition: all 0.25s cubic-bezier(0.31, -0.105, 0.43, 1.4); }
    .button span:after {
      content: '';
      background-color: #a53125;
      width: 1px;
      height: 70%;
      position: absolute;
      top: 15%;
      right: 9px; }
  .button .icon {
    width: 33%;
    right: 0;
    transition: all 0.25s cubic-bezier(0.31, -0.105, 0.43, 1.4); }
    .button .icon .fa {
      font-size: 0.75rem;
      vertical-align: middle;
      transition: all 0.25s cubic-bezier(0.31, -0.105, 0.43, 1.4), height 0.25s ease; }
    .button .icon .fa-paper-plane {
      height: 15px; }
    .button .icon .fa-check {
      display: none; }
  .button.success span, .button:hover span {
    left: -72%;
    opacity: 0; }
  .button.success .icon, .button:hover .icon {
    width: 100%; }
    .button.success .icon .fa, .button:hover .icon .fa {
      font-size: 1rem; }
  .button.success {
    background-color: #27ae60; }
    .button.success .icon .fa-paper-plane {
      display: none; }
    .button.success .icon .fa-check {
      display: inline-block; }
  .button:hover {
    opacity: .9; }
    .button:hover .icon .fa-paper-plane {
      height: 20px; }
  .button:active {
    opacity: 1; }

