/* Pages Templates */
/* home */
.presentation {
  background-color: #fafafa;
  position: sticky;
  top: 0;
  height: 100vh;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  padding: 10%; }

.presentation__central {
  display: flex;
  flex-direction: column;
  flex: 6;
  justify-content: flex-end; }

.presentation__links {
  flex: 1.1; }

.presentation__icons {
  color: #333333;
  margin-right: 1rem; }

.presentation__icons--item {
  font-size: 1.1rem;
  float: left; }

.presentation__icons--item::before {
  display: inline-block;
  font-style: normal;
  font-variant: normal;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased; }

.presentation__email {
  display: flex;
  flex-direction: column; }

@media (max-width: 991px) {
